.dashboard-container{
    width: 50%;
}

.dashboard{
width: calc(100% - 56px);
height: 100%;
margin: auto;
padding: 28px;
display: flex;
flex-direction: column;
justify-content: center;
}
 
.dashboard-search-bar {
    background-color: #3C3F54;
    width: 100%;
    border-radius: 15px;
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 1rem 2rem;
    margin: 28px 0;
    font-size: 22px;
    font-weight: 600;
    color: white;
  }
  
  .dashboard-search-bar .search-icon {
    margin-right: 12px;
  }
  
  .dashboard-search-bar input {
    background-color: inherit;
    border: none;
    outline: none;
    font-size: 22px;
    color: white;
    font-weight: bold;
    opacity: 0.7;
  }


.search-bar-container{
    display: flex;
    flex-direction: row;
    width: 100%;
}
.search-bar-search{
    width: 50%;
    background-color: #d2d2d4;
    border-radius: 40px;
    display: flex;
    height: 40px;
    flex-direction: row;
    align-items: center;
    padding: 5px 10px;
    margin:  0 10px 0 0;
    font-size: 11px;
    font-weight: normal;
}


.search-bar-search .search-icon {
    margin-right: 12px;
    color: #262A41;
    /* font-weight: bold; */
    opacity: .52;
    font-size: 15px;
  }
  
.search-bar-search input {
    background-color: inherit;
    border: none;
    outline: none;
    color: #262A41;
    font-weight: bold;
    opacity: .52;
}

.search-bar-search input::-webkit-input-placeholder {
    opacity: 1;
    color: #262A41;
    opacity: .52;
    font-size: 11px;
}

.search-bar-search input:-ms-input-placeholder {
    opacity: 1;
    color: #262A41;
    opacity: .52;
    font-size: 11px;
}

.search-bar-search input::-ms-input-placeholder {
    opacity: 1;
    color: #262A41;
    opacity: .52;
    font-size: 11px;
}

.search-bar-search input::placeholder {
    opacity: 1;
    color: #262A41;
    opacity: .52;
    font-size: 11px;
}

.search-bar-select-country{
    background-color: #d2d2d4;
    border-radius: 40px;
    padding: 0 5px;
    height: 40px;
    margin-right: 10px;
}

.search-bar-select-country select{
    background:none;
    padding: 10px;
    outline: none;
    border: none;
    color: #262A41;
    opacity: .52;
    font-size: 12px;
}

.search-bar-container input[type="checkbox"]{
    position: relative;
    width: 50px;
    height: 25px;
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    background: #c6c6c6;
    outline: none;
    border-radius: 20px;
    transition: 0.5s;
    box-shadow: inset 0 0 5px rgba(0,0,0,0.2);
}

.search-bar-container input:checked[type="checkbox"]{
    background: #30BBB5;
}

.search-bar-container input[type="checkbox"]::before{
    content: '';
    position: absolute;
    width: 25px;
    height: 25px;
    border-radius: 20px;
    top: 0;
    left: 0;
    transform: scale(0.8);
    background: #fff;    
    transition: 0.5s;
}

.search-bar-container input:checked[type="checkbox"]::before{
    left: 25px;
}

.search-bar-country-checkbox{
    display: flex;
    flex-direction: row;
    align-items: center;
}

.search-bar-country-checkbox p{
    font-size: 12px;
}


